@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Russo+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background-color: black;
  background-image: url('https://miro.medium.com/max/3840/1*PyExFMlUFx1Yv_28Nd1BRA.png');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
}

.App {
  min-height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.timeline-fakebox{height:80px}.vertical-timeline-element{font-family:"Roboto", sans-serif;color:#949494;margin-bottom:7rem;margin-top:7rem;padding:0.1rem;transition:500ms}.vertical-timeline-element-content{display:flex;flex-direction:column;width:100%;box-shadow:1px 0px 5px 5px rgba(255,255,255,0.5) inset;padding:0.1rem;background-color:#000;transition:500ms}.vertical-timeline-element-content:hover{color:white;transition:500ms}.vertical-timeline-element-title{text-align:left;margin-bottom:1rem;width:100%;text-decoration:underline}.vertical-timeline-element-desc{text-align:left;padding:0.2rem;border-top:1px solid grey;border-bottom:1px solid grey}.vertical-timeline-element-image{max-width:400px;max-height:200px;align-self:center}.vertical-timeline-element-techs{display:flex;margin:0;margin-bottom:0.5rem;padding:0;border-top:1px solid grey;border-bottom:1px solid grey;width:100%}.vertical-timeline-element-techs__title{text-align:left;padding:0;margin:0}.vertical-timeline-element-techs__button{border:1px solid grey;box-shadow:1px 0px 1px 1px rgba(0,0,0,0.5) inset;background-color:#00e4ef;color:#727070;border-radius:10px;padding:0;margin:0.3rem;max-height:40px}.vertical-timeline-element-techs__button--text{padding:0.2rem;margin:0.2rem}.vertical-timeline-element--work{max-height:400px}.thumbnail iframe{width:1440px;height:900px}.thumbnail{max-height:250px;position:relative;-ms-zoom:0.25;-moz-transform:scale(0.25);-moz-transform-origin:0 0;-o-transform:scale(0.25);-o-transform-origin:0 0;-webkit-transform:scale(0.25);-webkit-transform-origin:0 0}

.navbar{font-family:'Russo One', sans-serif;display:flex;flex-direction:row;justify-content:space-between;background-color:black;height:80px;top:0;width:100%;position:sticky;z-index:1;box-shadow:1px 9px 21px 0px rgba(255,243,243,0.75);opacity:0.9;transition:500ms}.navbar:hover{opacity:1}.navbar__logo{align-self:center;max-height:80px;color:white;cursor:pointer;margin-left:1rem;transition:500ms}.navbar__logo:hover{border-radius:50px;background-color:rgba(104,104,104,0.556);transition:500ms}.navbar--scrolled{background:#000;opacity:0.5;transition:500ms}.navbar-right{margin:0.65rem;margin-right:1rem}.navbar-right>a:hover{fill:blue}.navbar-middle{margin:1rem;font-size:2rem}.navbar-middle>a{text-decoration:none;color:white;margin:1rem;transition:500ms}.navbar-middle>a:hover{color:#00e4ef;transition:500ms}

.about{font-family:"Roboto", sans-serif;display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;height:100vh;background-color:transparent}.about-container{display:flex;width:50%;box-shadow:1px 0px 5px 5px rgba(255,255,255,0.5) inset;background-color:black;color:white;border-radius:20px;margin-bottom:10rem}.about-pic{margin:1rem 3rem 1rem}.about-pic>img{border-radius:50%;max-width:150px}.about-text{padding:2rem 3rem 2rem;text-align:center}.about-down{margin:0;padding:0;cursor:pointer;height:4rem;width:4rem;background-color:black;border-radius:50%;position:absolute;bottom:1rem;width:4rem;width:var(--logo-size);height:4rem;height:var(--logo-size);border-radius:100%;position:absolute;top:700px;top:var(--offset);left:calc(50% - 4rem / 2);left:calc(50% - var(--logo-size) / 2);-webkit-animation-name:bounce;animation-name:bounce;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-direction:alternate;animation-direction:alternate;-webkit-animation-timing-function:cubic-bezier(0.95, 0.05, 0.795, 0.035);animation-timing-function:cubic-bezier(0.95, 0.05, 0.795, 0.035);-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}:root{--drop-height: 50px;--logo-size: 4rem;--offset: 700px;--shadow-height: 8px}@-webkit-keyframes bounce{from{-webkit-transform:translateY(0) scale(1);transform:translateY(0) scale(1)}to{-webkit-transform:translateY(50px) scale(1, 0.7);transform:translateY(50px) scale(1, 0.7);-webkit-transform:translateY(var(--drop-height)) scale(1, 0.7);transform:translateY(var(--drop-height)) scale(1, 0.7)}}@keyframes bounce{from{-webkit-transform:translateY(0) scale(1);transform:translateY(0) scale(1)}to{-webkit-transform:translateY(50px) scale(1, 0.7);transform:translateY(50px) scale(1, 0.7);-webkit-transform:translateY(var(--drop-height)) scale(1, 0.7);transform:translateY(var(--drop-height)) scale(1, 0.7)}}

